import textLogoIcon from '@/assets/logo-text-white.svg';
import Button, { ButtonTheme } from '@/components/button';
import arrowLeftIcon from '@/assets/arrow-left.svg';
import Layout, { LayoutTheme } from '@/components/Layout';
import { useHistory } from '@jupiter/plugin-runtime/router';
import { api } from '@/utils';
import store from 'store';
import { questions, STORAGE_KEYS } from '@/utils/contants';
import Logo from '@/components/Logo';
import styles from './index.module.css';

const Join = () => {
  const history = useHistory();

  const submit = async (isJoin: boolean) => {
    const info = Object.keys(STORAGE_KEYS).reduce((memo, key) => {
      if (key === 'IDENTITY') {
        // eslint-disable-next-line no-param-reassign
        memo = {
          ...memo,
          ...store.get(STORAGE_KEYS[key]),
        };
      } else {
        // eslint-disable-next-line no-param-reassign
        memo = {
          ...memo,
          // @ts-expect-error
          [key]: store.get(STORAGE_KEYS[key]),
        };
      }

      return memo;
    }, {});

    (info as any).joinInternalTest = isJoin;

    console.log('submit ->', info);

    const { code, msg } = await api.run('update-research-item', info);

    if (code === 200) {
      store.clearAll();
      store.set('has_completed_modern_js_questionnaire', true);
      history.push('/success');
    } else {
      // TODO: 提示信息优化
      throw new Error(`request error : ${msg}`);
    }
  };

  const toPrevStep = () => {
    // TODO: 需要回退到最后一步
    history.push(`/questions/${questions.length - 1}`);
  };

  return (
    <Layout theme={LayoutTheme.Dark} progress="100%">
      <div className={styles.join}>
        <div className={styles.joinHeader}>
          <img src={arrowLeftIcon} className={styles.headerIcon} />
          <p className={styles.headerTitle} onClick={toPrevStep}>
            返回上一步
          </p>
        </div>
        <div className={styles.joinMain}>
          <div className={styles.joinLogo}>
            <Logo theme={LayoutTheme.Dark} />
          </div>
          <p className={styles.joinTitle}>您是否愿意参加 Modern.js 开源内测</p>
          <div className={styles.buttonGroup}>
            <Button
              colorTheme={ButtonTheme.Light}
              onClick={() => submit(true)}
              width={142}
              height={42}
              zIndex={1}>
              👌 我愿意
            </Button>
            <Button
              colorTheme={ButtonTheme.Dark}
              onClick={() => submit(false)}
              width={142}
              height={42}
              zIndex={1}>
              我不愿意
            </Button>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Join;
