import { Switch, Route } from '@jupiter/plugin-runtime/router';
import './App.global.css';
import Home from '@/containers/Home';
import Questions from '@/containers/Questions';
import Success from '@/containers/Success';
import Login from '@/containers/Login';
import Join from '@/containers/Join';
import { MouseContextProvider } from '@/common/context/mouseContext';

const App: React.FC = () => {
  return (
    <MouseContextProvider>
      <Switch>
        <Route exact={true} path="/" component={Home} />
        <Route exact={true} path="/login" component={Login} />
        <Route
          exact={true}
          path="/questions/:currentStep"
          component={Questions}
        />
        <Route exact={true} path="/success" component={Success} />
        <Route exact={true} path="/join" component={Join} />
        <Route path="*">
          <div>404</div>
        </Route>
      </Switch>
    </MouseContextProvider>
  );
};

export default App;
