import { url } from 'inspector';
import Footer from '@/components/Footer';
import React from 'react';
import whiteBgIcon from '@/assets/white-bg.svg';
import styles from './index.module.css';

export enum LayoutTheme {
  Dark,
  Light,
}

export interface LayoutProps {
  children: React.ReactNode;
  theme?: LayoutTheme;
  progress?: number | string;
  disableBackgroudImage?: boolean;
}

const Layout = ({
  children,
  theme,
  progress,
  disableBackgroudImage = false,
}: LayoutProps) => {
  const backgroundStyle =
    theme === LayoutTheme.Dark
      ? {
          background: '#262627',
        }
      : {
          background: disableBackgroudImage
            ? '#fff'
            : `url(${whiteBgIcon}) repeat`,
        };

  return (
    <div className={styles.wrapper} style={backgroundStyle}>
      {progress ? (
        <div className={styles.progressBar} style={{ width: progress }}></div>
      ) : null}
      <div className={styles.main}>
        <div className={styles.container}>{children}</div>
        <Footer theme={theme} />
      </div>
    </div>
  );
};

export default Layout;
