import logoTextIcon from '@/assets/logo-text.svg';
import Input from '@/components/Input';
import Button, { ButtonTheme } from '@/components/button';
import Layout, { LayoutTheme } from '@/components/Layout';
import { useHistory } from '@jupiter/plugin-runtime/router';
import store from 'store';
import React, { useEffect, useState } from 'react';
import { STORAGE_KEYS } from '@/utils/contants';
import Logo from '@/components/Logo';
import styles from './index.module.css';

const Login = () => {
  const history = useHistory();

  const [identity, setIdentity] = useState({
    email: '',
    wx: '',
    github: '',
  });

  const [isButtonDisabled, setButtonDisabled] = useState(true);

  useEffect(() => {
    const initialValue = store.get(STORAGE_KEYS.IDENTITY);
    if (initialValue) {
      setIdentity(initialValue);
    }
  }, []);

  useEffect(() => {
    if (!identity.email && !identity.wx && !identity.github) {
      setButtonDisabled(true);
    } else {
      setButtonDisabled(false);
    }
  }, [identity]);

  const toQuestions = () => {
    if (!isButtonDisabled) {
      store.set(STORAGE_KEYS.IDENTITY, identity);
      history.push('/questions/0');
    }
  };

  const handleInputChange = (key: string, e: React.BaseSyntheticEvent) => {
    setIdentity(prev => ({
      ...prev,
      [key]: e.target.value,
    }));
  };

  return (
    <Layout>
      <div className={styles.login}>
        <div className={styles.logoText}>
          <Logo theme={LayoutTheme.Light} />
        </div>
        <p className={styles.loginTitle}>现代Web开发者问卷调查</p>
        <p className={styles.loginDesc}>问卷调查报告会公开发布</p>
        <div className={styles.inputGroup}>
          <Input
            name="email"
            label="邮箱"
            onChange={e => handleInputChange('email', e)}
            value={identity.email}
          />
          <Input
            name="wx"
            label="微信号"
            onChange={e => handleInputChange('wx', e)}
            value={identity.wx}
          />
          <Input
            name="github"
            label="Github 主页"
            onChange={e => handleInputChange('github', e)}
            value={identity.github}
          />
        </div>
        <Button
          colorTheme={ButtonTheme.Dark}
          width={330}
          onClick={toQuestions}
          disabled={isButtonDisabled}>
          立即开始
        </Button>
      </div>
    </Layout>
  );
};

export default Login;
