import styled from '@jupiter/plugin-runtime/styled';

export interface InputProps {
  name: string;
  label: string;
  value: string;
  onChange: React.ChangeEventHandler;
  placeholder?: string;
}

const StyledDiv = styled.div`
  width: 100%;
  position: relative;
`;

const StyledLabel = styled.label`
  height: 22px;
  line-height: 22px;
  color: #262627;
  font-weight: 500;
  font-size: 14px;
  display: block;
  margin-bottom: 8px;
`;

const StyledInput = styled.input`
  width: 100%;
  border: 1px solid #000000;
  outline: none;
  height: 36px;
  padding-left: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: #000000;
  background-color: transparent;

  &:hover,
  &:focus,
  &:active {
    border: 1px solid #ffac46;
  }
`;

const Input = ({ label, name, onChange, value, placeholder }: InputProps) => {
  return (
    <StyledDiv>
      <StyledLabel htmlFor={name}>{label}</StyledLabel>
      <StyledInput
        type="input"
        id={name}
        name={name}
        onChange={onChange}
        value={value}
        placeholder={placeholder || '选填'}
      />
    </StyledDiv>
  );
};

export default Input;
